.LoaderWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.Loader {
  border: 6px solid rgba(0, 0, 0, 0.1);
  border-left-color: #3f51b5;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
